  }
}
@-o-keyframes move {
  0% {
    top: 0;
    opacity: 0;
  }
  50% {
    top: 50%;
    opacity: 1;
  }
  100% {
    top: 90%;
    opacity: 0;
  }
}
@keyframes move {
  0% {
    top: 0;
