:root {
  --fonSizeAllDocument: 16px;
  --partColorLogoBlue: #2196f3;
  --colorBlackMenu: #212121;
  --colorlinkHeader: #757575;
  --colorTitle: #212121;
  --colorMenuHover: #2196f3;
  --colorportfolioItemHover: rgba(33,150,243,0.9);
  --colorButton: #2196f3;
  --colorBlack: #000;
  --colorWhite: #fff;
  --colorText: #757575;
  --colorLinkFooter: rgba(255,255,255,0.6);
  --bgColorLinkFooterSocialIcon: rgba(255,255,255,0.1);
  --bgColorTopBanner: rgba(47,48,58,0.4);
  --bgColorFooter: #2f303a;
  --bgColorAdvantagesImage: #f5f4fa;
  --bgColorOurTeamSection: #f5f4fa;
  --colorSocialIcon: #afb1b8;
  --borderColorTabs: #eee;
  --backdropColor: rgba(0,0,0,0.2);
  --colorBorderClosePopup: rgba(0,0,0,0.1);
  --colorBorderPopupField: rgba(33,33,33,0.2);
  --borderHeader: #ececec;
  --colorBorderInputFooter: rgba(255,255,255,0.3);
/*color page portfolio*/
  --colorbuttonDafault: #f5f4fa;
  --colorbuttonActive: #2196f3;
  --colortextButton: #212121;
  --boxShadowActiveButton: 0px 3px 1px rgba(0,0,0,0.1), 0px 1px 2px rgba(0,0,0,0.08), 0px 2px 2px rgba(0,0,0,0.12);
  --boxShadowCardItemOurCompany: 0px 1px 3px rgba(0,0,0,0.12), 0px 1px 1px rgba(0,0,0,0.14), 0px 2px 1px rgba(0,0,0,0.2);
/*font family*/
  --ffReg: "Roboto";
  --ffBold: "RobotoBold";
  --ffBlack: "RobotoBlack";
  --ffMedium: "RobotoMedium";
  --ffLogo: "RalewayBold";
}
.body__hidden {
  overflow: hidden;
}
body {
  font-family: var(--ffReg);
  font-size: var(--fonSizeAllDocument);
}
h1 {
  font-size: 44px;
  line-height: 1.36;
  letter-spacing: 0.06em;
  text-align: center;
  font-family: var(--ffBlack);
  text-transform: uppercase;
  color: var(--colorWhite);
}
@media screen and (max-width: 991px) {
  h1 {
    font-size: 3.4vw;
    line-height: 1.6;
    letter-spacing: 0.063em;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  h1 {
    font-size: 5.6vw;
  }
}
h2,
h3,
h4,
h5,
h6 {
  color: var(--colorTitle);
  font-family: var(--ffBold);
}
p {
  color: var(--colorText);
}
img {
  max-width: 100%;
  display: block;
}
.main-page h2 {
  font-size: 36px;
  line-height: 1.16;
  letter-spacing: 0.03em;
  text-align: center;
}
@media screen and (max-width: 991px) {
  .main-page h2 {
    font-size: 3.7vw;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .main-page h2 {
    font-size: 6vw;
  }
}
.wrapper {
  width: calc(1600px - (200px * 2));
  margin: 0 auto;
  padding: 0 15px;
}
@media screen and (max-width: 1200px) {
  .wrapper {
    width: 100%;
    font-family: "Roboto";
  }
}
main {
  margin-top: 80px;
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  main {
    margin-top: 13vw;
  }
}
.advantages,
.what-we-doing,
.our-team,
.jobs,
.customers {
  padding: 94px 0;
}
@media screen and (max-width: 1200px) {
  .advantages,
  .what-we-doing,
  .our-team,
  .jobs,
  .customers {
    padding: 6.05vw 0;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .advantages,
  .what-we-doing,
  .our-team,
  .jobs,
  .customers {
    padding: 7.75vw 0;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .advantages,
  .what-we-doing,
  .our-team,
  .jobs,
  .customers {
    padding: 12.75vw 0;
  }
}
@media screen and (max-width: 1200px) {
  .advantages {
    padding-bottom: 3.025vw;
  }
}
.what-we-doing__image-items,
.our-team__list-items {
  margin-top: 50px;
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  address {
    text-align: center;
  }
}
.mobile {
  display: none !important;
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .mobile {
    display: block !important;
  }
}
.mobile-flex {
  display: none !important;
}
@media screen and (max-width: 991px) {
  .mobile-flex {
    display: flex !important;
  }
}
.desktop {
  display: block !important;
}
@media screen and (max-width: 991px) {
  .desktop {
    display: none !important;
  }
}
.desktop-flex {
  display: flex !important;
}
@media screen and (max-width: 991px) {
  .desktop-flex {
    display: none !important;
  }
}
header {
  background-color: var(--colorWhite);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  height: 80px;
  display: flex;
  align-items: center;
  z-index: 10;
  border-bottom: 1px solid var(--borderHeader);
}
@media screen and (max-width: 991px) {
  header {
    height: auto;
  }
}
header .wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .wrapper nav {
  display: flex;
  justify-content: space-between;
  width: 35%;
}
@media screen and (max-width: 991px) {
  header .wrapper nav {
    display: flex;
    justify-content: space-between;
    flex-basis: 70%;
    align-items: center;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  header .wrapper nav {
    flex-basis: 100%;
    width: 100%;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .main-header {
    height: 13vw;
  }
}
.main-header__menu {
  display: flex;
  flex-basis: 100%;
  justify-content: space-between;
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .main-header__menu {
    background-color: var(--colorWhite);
    position: absolute;
    left: 0;
    right: 0;
    top: 13vw;
    padding: 10vw;
    box-shadow: 0px 4px 8px rgba(0,0,0,0.04), 0px 2px 4px rgba(0,0,0,0.08), 0px 1px 3px rgba(0,0,0,0.16);
    flex-direction: column;
    transform: scale(1);
    visibility: visible;
    transition: visibility, transform, 0.3s;
    align-items: center;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .main-header--is-hidden {
    visibility: hidden;
    transform: scale(0);
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
  }
}
.logo {
  text-decoration: none;
  font-family: var(--ffLogo);
  color: var(--colorBlackMenu);
  font-size: 26px;
  line-height: 1.19;
  letter-spacing: 0.03em;
  margin-right: 93px;
}
@media screen and (max-width: 991px) {
  .logo {
    font-size: 3.22vw;
    margin-right: 11.5vw;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .logo {
    margin-right: 10.7vw;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .logo {
    font-size: 5.02vw;
    margin-right: 0;
  }
}
.logo--blue-color {
  color: var(--partColorLogoBlue);
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .mobile-menu--toggle {
    border: none;
    background: none;
  }
}
.mobile-menu__icon-close {
  display: none;
}
.mobile-menu__is-open .mobile-menu__icon-close {
  display: block;
}
.mobile-menu__is-open .mobile-menu__icon-open {
  display: none;
}
.main-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1 0 70%;
}
@media screen and (max-width: 991px) {
  .main-menu {
    flex: none;
    flex-basis: 57%;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .main-menu {
    flex-basis: 80%;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .main-menu {
    flex-direction: column;
    flex-basis: 100%;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .main-menu__item {
    margin-bottom: 3.9vw;
  }
}
.main-menu__item a {
  text-decoration: none;
  color: var(--colorBlackMenu);
  transition: 0.3s;
  font-family: var(--ffMedium);
  padding: 31px 0;
  position: relative;
}
@media screen and (max-width: 991px) {
  .main-menu__item a {
    padding: 4.1vw 0;
    font-size: 1.9vw;
    display: block;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .main-menu__item a {
    padding: 2.9vw 0;
    font-size: 4.4vw;
  }
}
.main-menu__item a:after {
  content: "";
  width: 0;
  height: 4px;
  background-color: var(--colorButton);
  z-index: 11;
  display: block;
  position: absolute;
  bottom: 0;
  border-radius: 2px;
  transition: width, cubic-bezier(0.4, 0, 0.2, 1) 0.25s;
}
@media screen and (max-width: 991px) {
  .main-menu__item a:after {
    bottom: -1px;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .main-menu__item a:after {
    bottom: 0;
  }
}
.main-menu__item a:focus {
  color: var(--colorMenuHover);
}
.main-menu__item a:focus:after {
  width: 100%;
}
.main-menu__item:hover .main-menu__link {
  color: var(--colorMenuHover);
}
.main-menu__item:hover .main-menu__link:after {
  width: 100%;
}
.main-menu__item--current .main-menu__link {
  color: var(--colorMenuHover);
}
.main-menu__item--current .main-menu__link:after {
  width: 100%;
}
.communication {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (max-width: 991px) {
  .communication {
    width: auto;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .communication {
    align-items: center;
    margin-top: 4vw;
  }
}
.communication__icon {
  margin-right: 10px;
  fill: var(--colorlinkHeader);
  transition: 0.3s;
}
.communication__list-item:hover .communication__icon {
  fill: var(--colorMenuHover);
}
.communication__list-item:first-child {
  margin-right: 30px;
}
@media screen and (max-width: 991px) {
  .communication__list-item:first-child {
    margin-right: auto;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .communication__list-item {
    margin-top: 0.8vw;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .communication__list-item {
    margin: 0 auto;
  }
}
.communication__link {
  text-decoration: none;
  color: var(--colorlinkHeader);
  transition: 0.3s;
  font-family: var(--ffMedium);
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  display: flex;
  align-items: center;
}
@media screen and (max-width: 991px) {
  .communication__link {
    font-size: 1.6vw;
    line-height: 2;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .communication__link {
    font-size: 3.8vw;
  }
}
.communication__link:focus,
.communication__link:hover {
  color: var(--colorMenuHover);
}
.communication__link:focus .communication__icon {
  fill: var(--colorMenuHover);
}
.top-banner {
/* padding: 350px 0 200px; */
  background: var(--bgColorTopBanner) url("../images/top-banner.jpg") no-repeat top center;
  background-size: cover;
  height: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 991px) {
  .top-banner {
    padding: 18vw 0 15.3vw;
    background: var(--bgColorTopBanner) url("../images/top-banner_tablet.jpg") no-repeat top center;
    background-size: cover;
    height: auto;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .top-banner {
    padding: 15.5vw 0 15vw;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .top-banner {
    padding: 25.5vw 0 26vw;
  }
}
.open-popup {
  background: var(--colorButton);
  border-radius: 4px;
  box-shadow: 0 4px 4px rgba(0,0,0,0.15);
  border: none;
  color: var(--colorWhite);
  margin: auto;
  display: block;
  padding: 10px 32px;
  font-size: 16px;
  line-height: 1.87;
  letter-spacing: 0.06em;
  font-family: var(--ffBold);
  cursor: pointer;
  margin-top: 30px;
}
@media screen and (max-width: 991px) {
  .open-popup {
    padding: 1.3vw 4.2vw;
    font-size: 2.2vw;
    line-height: 2;
    letter-spacing: 0.06em;
    margin-top: 4vw;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .open-popup {
    padding: 1.3vw 4.2vw;
    font-size: 2.1vw;
    margin-top: 4vw;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .open-popup {
    font-size: 4.1vw;
    margin-top: 6vw;
  }
}
@media screen and (max-width: 1200px) {
  .advantages {
    padding-bottom: 3.025vw;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .advantages {
    padding: 12.025vw 0 6vw;
  }
}
.advantages__list-items {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}
.advantages__list-item {
  width: calc(100% / 4 - 30px);
  margin: 0 15px;
}
@media screen and (max-width: 991px) {
  .advantages__list-item {
    width: calc(100% / 3 - 30px);
    margin: 0 15px 30px;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .advantages__list-item {
    width: calc(100% / 2 - 30px);
    margin: 0 15px 30px;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .advantages__list-item {
    width: 100%;
    margin-bottom: 5vw;
  }
}
.advantages__image {
  background-color: var(--bgColorAdvantagesImage);
  border-radius: 4px;
  text-align: center;
  padding: 25px 100px;
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .advantages__image {
    padding: 3.1vw 10vw;
  }
}
.advantages__image img {
  width: 70px;
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .advantages__icon {
    width: 15vw;
  }
}
.advantages h3,
.advantages p {
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.03em;
}
@media screen and (max-width: 1200px) {
  .advantages h3,
  .advantages p {
    font-size: 1.7vw;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .advantages h3,
  .advantages p {
    font-size: 1.9vw;
    letter-spacing: 0.01em;
