.popup__form-input#form-checkbox {
  width: 15px;
  height: 15px;
}
.popup__form-textarea {
  padding: 12px 16px;
  width: 100%;
  resize: none;
  min-height: 120px;
}
.form-group {
  display: block;
  margin-top: 20px;
  margin-bottom: 30px;
}
.form-group__label {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 14px;
  line-height: 1.71;
  position: relative;
  cursor: pointer;
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .form-group__label {
    display: block;
    font-size: 12px;
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .form-group__label {
    display: block;
    font-size: 12px;
  }
}
.form-group__label:before {
  content: "";
  -webkit-appearance: none;
  background-color: var(--colorWhite);
  border: 2px solid var(--colortextButton);
  padding: 10px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  border-radius: 4px;
  margin-right: 5px;
}
.form-group__link {
  margin-left: 5px;
  color: var(--colorButton);
}
.form-group__checkbox {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.03em;
  color: var(--colorText);
  text-transform: none;
  font-family: inherit;
  margin-top: 20px;
  text-align: center;
}
.form-group__checkbox:checked + .form-group__label:after {
  content: "";
  display: block;
  position: absolute;
  top: 2px;
  left: 9px;
  width: 6px;
  height: 14px;
  border: solid var(--colorWhite);
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
.form-group__checkbox:checked + .form-group__label:before {
  background: var(--colorButton);
  border: 2px solid var(--colorButton);
}
.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: translate(-100%, -100%);
}
.backdrop {
  background: var(--backdropColor);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  transition: cubic-bezier(0.4, 0, 0.2, 1) transform, opacity, visibility, 0.25s;
  transform: translate(0%, 0%);
  z-index: 10;
}
.backdrop.is-hidden {
  transform: translate(100%, 100%);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}
.popup__close {
  position: absolute;
  right: 8px;
  top: 8px;
  border: 1px solid var(--colorBorderClosePopup);
  border-radius: 100%;
  padding: 10px;
}
.popup__close:hover,
.popup__close:focus {
  fill: var(--colorMenuHover);
}
.form__submit {
  margin: 0 auto;
  display: table;
  padding: 10px 55px;
  border: none;
  font-size: 16px;
  line-height: 1.87;
  text-align: center;
  letter-spacing: 0.06em;
  background: var(--colorButton);
  box-shadow: 0 4px 4px rgba(0,0,0,0.15);
  border-radius: 4px;
  color: var(--colorWhite);
  font-family: var(--ffBold);
}
